*,
*::before,
*::after {
  box-sizing: border-box;
}
*:focus {
  outline: 3px #228bec;
  outline-offset: 0;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
  color: #4d4d4d;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: teal;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
html {
  font: 90% / 1.15 sans-serif;
}
h1,
h2 {
  margin-bottom: 0;
}
ul {
  list-style: none;
  padding: 0;
}
button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
}
button:hover {
  box-shadow: 0 0 0 2px teal;
  border-color: teal;
}
.checked {
  box-shadow: 0 0 0 2px turquoise;
}
h1,
h2 {
  margin-bottom: 0;
}
body {
  width: 100%;
  max-width: 68rem;
  margin: 0 auto;
  font: 1.2rem/1.25 Arial, sans-serif;
  background-color: #f5f5f5;
  color: #4d4d4d;
}
textarea {
  overflow: auto;
  white-space: pre-wrap;
  border: transparent;
  width: 100%;
  min-height: 19rem;
  font: 1.2rem/1.25 Arial, sans-serif;
  background-color: #f5f5f5;
  color: #4d4d4d;
  padding: 0;
  resize: none;
  cursor: auto;
}

.App {
  width: 100%;
  min-height: 50rem;
  text-align: center;
  border: 0.3rem solid;
  position: relative;
}
.App-header {
  position: absolute;
  border: 0.3rem solid;
  border-top: 0;
  width: 100%;
  max-width: 20rem;
  text-align: center;
  margin: 0 0 3rem 24rem;
}
.add-button {
  float: right;
  margin-left: 6.6rem;
  padding: 0.5rem 1rem;
  border: 0.3rem solid #4d4d4d;
  border-top: 0;
  border-right: 0;
  text-transform: capitalize;
  cursor: pointer;
  font-size: 2rem;
}
.sort-btns {
  float: left;
  margin-right: 6.6rem;
  padding: 0.5rem 1rem;
  border: 0.3rem solid #4d4d4d;
  border-top: 0;
  border-left: 0;
  border-bottom: 0;
  cursor: pointer;
}
.reverse-btn {
  position: absolute;
  left: 4.9rem;
  max-width: 3.6rem;
  float: left;
  cursor: pointer;
  padding: 0.3rem 0.3rem;
  border: 0.3rem solid #4d4d4d;
  border-left: 0;
  border-top: 0;
}
.arrow {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
}

.sort-btns > button {
  width: 2rem;
  height: 2rem;
}
.sort-btn-red {
  background-color: red;
}
.sort-btn-green {
  background-color: green;
}
.sort-btn-blue {
  background-color: blue;
}

.timestamp {
  border-bottom: 0.3rem solid;
  display: inline-flex;
  font-size: 1.6rem;
}

.btn-group {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.btn:nth-child(odd) {
  border-left: 0;
}
.btn:nth-child(even) {
  border-right: 0;
}
.btn {
  border: 0.3rem solid;
  border-bottom: 0;
  min-width: 7rem;
}
.lightcoral {
  padding: 1rem;
  border: 0.2rem solid #4d4d4d;
  background-color: lightcoral;
}
.lightgreen {
  padding: 1rem;
  border: 0.2rem solid #4d4d4d;
  background-color: lightgreen;
}
.lightblue {
  padding: 1rem;
  border: 0.2rem solid #4d4d4d;
  background-color: lightblue;
}
.white {
  padding: 1rem;
  border: 0.2rem solid #4d4d4d;
  background-color: white;
}
.date-div {
  position: absolute;
  text-align: center;
  width: 100%;
  margin-top: 6.6rem;
  top: 0.05rem;
}
.note-stack {
  border-top: 0.3rem solid;
  margin-top: 9rem;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.note {
  width: 20rem;
  border: 0.3rem solid;
  margin: 1.2rem auto;
}
.note-text {
  white-space: pre-wrap;
  padding: 0.5rem 0.8rem;
  min-height: 19rem;
  text-align: left;
  max-height: 19rem;
  overflow: auto;
  hyphens: auto;
}

@media screen and (min-width: 900px) {
  .note-stack {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 26.3rem;
  }
}
@media screen and (max-width: 899px) {
  .note-stack {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 26.3rem;
  }
  .App {
    max-width: 55rem;
  }
}

@media screen and (max-width: 660px) {
  .note-stack {
    position: relative;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-auto-rows: 26.3rem;
  }
  .App {
    max-width: 40rem;
  }
  .App-header {
    margin-left: 10rem;
  }
}
